.spoler_title {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.spoler_title:after {
  content: "";
  position: absolute;
  top: 11px;
  height: 1px;
  width: 10px;
  background-color: #000;
  transition: all .3s ease 0s;
}

.spoler_title:before {
  left: 0;
  transform: rotate(40deg);
}

.spoler_title:after {
  left: 8px;
  transform: rotate(-40deg);
}

.spoler_text {
  display: none;
  padding: 10px 0 0 0;
}

.spoler_title1.active:after {
  background-color: red;
}

.spoler_title1.active:before {
  transform: rotate(-40deg);
}

.spoler_title1.active:after {
  transform: rotate(40deg);
}

.spoler_title2.active:after {
  background-color: red;
}

.spoler_title2.active:before {
  transform: rotate(-40deg);
}

.spoler_title2.active:after {
  transform: rotate(40deg);
}

.spoler_title3 {
  position: relative;
  padding-left: 40px;
  cursor: pointer;
  text-transform: uppercase;
}

.spoler_title3:before {
  content: "";
  position: absolute;
  top: 0;
  left: -1px;
  height: 28px;
  width: 28px;
  background-image: url(../img/arrows/accordion-plus.png);
  background-repeat: no-repeat;
  transition: all .3s ease 0s;
}

.spoler_title3.active:before {
  background-image: url(../img/arrows/accordion-minus.png);
}

