  min-height: 100vh;
  justify-content: center;
  align-items: center;
}

.transparent_text .content_text {
  text-align: center;
}

.transparent_text .content_text h2 {
  background-attachment: fixed;
  background-image: url(/img/s1200.webp);
  font-size: 10vw;
  font-weight: 900;
  line-height: 1em;
  text-transform: uppercase;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.transparent_text .content_text p {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}

.transparent_text .content_text a {
