.cont_boxing_anim {
  position: relative;
  display: flex;
  margin: 40px 0;
  background: #000;
  justify-content: center;
  align-items: center;
}

.cont_boxing_anim_items {
  position: relative;
  display: flex;
  height: 400px;
  width: 300px;
  color: #fff;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  -webkit-box-reflect: below 15px linear-gradient(transparent, transparent, rgba(0, 0, 0, 0.2));
}

.cont_boxing_anim_items .content {
  position: relative;
  z-index: 8;
  padding: 0 20px;
  color: #fff;
}

.cont_boxing_anim_items:hover .content {
  color: #000;
}

.cont_boxing_anim_items h3 {
  margin-bottom: 20px;
  text-align: center;
}

.cont_boxing_anim_items:before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  height: 100%;
  width: 100%;
  background: #000;
  border: 4px solid #fff;
  filter: url(#wavy);
}

.cont_boxing_anim_items:after {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  height: 100%;
  width: 50%;
  background: rgba(255, 255, 255, 0.1);
  border: 4px solid #fff;
  border-right: none;
  filter: url(#wavy);
}

.cont_boxing_anim_items:hover {
  color: #000;
}

.cont_boxing_anim_items:hover:before {
  background: #fff;
  transition: .5s;
}

.cont_boxing_anim_items:hover:after {
  background: #fffdd0;
  transition: .5s;
}

body {
