.fulpage {
  position: relative;
  min-height: 780px;
}

.fulpage_slider {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  height: 780px;
  width: 100%;
  border: 2px solid red;
}

.fulpage_slider .slide__fp {
  position: relative;
  overflow: hidden;
  height: 100%;
  background: #0f0;
  background-repeat: no-repeat;
  background-size: cover;
  border-right: 2px solid #000;
  transition: .5s ease-in-out;
  flex-grow: 1;
}

.fulpage_slider .slide__fp:hover {
  flex-grow: 5;
}

.fulpage_slider .slide__fp:last-child {
  border-right: none;
}

.fulpage_slider .slide__fp .content {
  position: absolute;
  bottom: 0;
  visibility: hidden;
  margin: 40px;
  padding: 30px;
  opacity: 0;
  background-color: #000;
  color: #fff;
  transition: 0s;
  transform: tranlateY(100px);
}

.fulpage_slider .slide__fp:hover .content {
  visibility: visible;
  opacity: 1;
  transition: .5s;
  transition-delay: .5s;
  transform: tranlateY(0);
}

.fulpage_slider .slide__fp:nth-child(1) {
  background-image: url(/img/tmb_12170_2582.jpg);
  background-position: center center;
}

